@charset "utf-8";

@import url('https://fonts.googleapis.com/css2?family=Noto+Sans:ital,wght@0,100..900;1,100..900&display=swap');

*{margin:0px;padding:0px;}
body{
  font-family: "Noto Sans", sans-serif;
  font-size:13px;
  color:#454545;
  background-color: #eff3f7;
}
a{
  color:#454545;text-decoration:none;
  font-size:16px;
}
ul li{list-style:none;}

/* 공통서식 */
/* nav서식 */
nav{
  padding-top:70px;
  text-align:center;  
}
.nav li{
  line-height:40px;
}
.nav a{
  text-decoration:none;
  font-size:16px;
  padding:5px 10px;
  color:#5d5d5d;
  text-shadow:0px 1px 1px #adadad;
}

.loading_bar{
  width:100%;height:100%;
  text-align:center;
}
.loading_bar img{
  position:relative;
  top:260px;
}

/* footer서식 */
  .footer{
    width:100%;line-height:50px;
    background:#333; 
  }
  .footer address{
    color:#fff;text-align:center;
    font-style:normal;
  }


/* 가로 해상도 1025px일 경우 서식 */
@media screen and (min-width:1025px){
  
}

/* 상단헤더 서식 */
  .header{
    width:200px;height:100%;
    text-align:center;
    position:fixed;
    top:0px;left:0px;
    z-index:100;
  }
  h1{padding:70px 0px 50px 0px;}

  .navi{line-height:40px;}
  .navi a{text-transform: uppercase;font-weight:bold;}
  .navi a:hover{text-decoration:underline;}

  /* 메인서식  */
  main{
    width:calc(100% - 200px);
    height:100%;
    position:fixed;
    left:200px;
    top:0px;
    /* padding-left:200px; */
    padding-top:50px;
    overflow:scroll;
    box-sizing:border-box;
  }
  main h2{
    padding-top:100px;
    text-align:center;
  }
  /* footer서식 */
  .footer{
    width:100%;line-height:40px;
    background:#333; 
    position:fixed;
    bottom:0px;left:0px;
  }
  .footer address{
    color:#fff;text-align:center;
    font-style:normal;
  }

